    font-weight: 400;
  }

  @font-face {
    font-family: "Inter";
    src: local("Inter-Medium"),
      local("Inter Medium"),
      url("Inter-Medium.woff2") format("woff2"),
      url("Inter-Medium.woff") format("woff");
    font-weight: 500;
  }